import {
  type GetNoumMembersQueryHookResult,
  type NoumMemberBasicFragment,
} from '@/apollo/graphql';
import { Avatar } from '@/components/Avatar/Avatar';
import { Button } from '@/components/Button';
import { DataGrid, type ActionsMenuItem } from '@/components/DataGrid';
import { type TableColumn } from '@/components/DataGrid/Table/Table';
import { Icon } from '@/components/Icon';
import { Infinite, getBottomStatusFromQuery } from '@/components/Infinite';
import { MultiselectField } from '@/components/MultiselectField';
import SkeletonLoaderProvider from '@/components/SkeletonLoader/SkeletonLoaderProvider';
import { TextField } from '@/components/TextField';
import { TSpan } from '@/components/Typography';
import { MemberRoleTag } from '@/features/noums/components/MemberRoleTag';
import { MemberStatusTag } from '@/features/noums/components/MemberStatusTag';
import { MembersManagerActionPermissions } from '@/features/noums/utils';
import { Stack } from '@/layout';
import { getLocalTimeFormatted } from '@/utils/date';
import { NetworkStatus } from '@apollo/client';
import { compact, debounce } from 'lodash';
import { useMemo } from 'react';
import { useModalManager } from '@/hooks/modal/useModalManager';
import { RolesInfoModal } from '@/features/noums/components/RolesInfoModal';
import { BulkActionsPopover } from './BulkActionsPopover';
import {
  useNoumMembersListFilters,
  type NoumMembersListQueryFilters,
} from './filters';
import { type BulkActionType, type RowActionType } from './types';

type ModalType = 'roles-info';

type NoumPageTableTypes = {
  onRowActionSelect: (
    value: RowActionType,
    item: NoumMemberBasicFragment,
  ) => void;
  networkStatus: NetworkStatus;
  fetchMore: GetNoumMembersQueryHookResult['fetchMore'];
  refetch: GetNoumMembersQueryHookResult['refetch'];
  data: NoumMemberBasicFragment[];
  currentCount: number;
  totalCount: number;
  rowsPerPage: number;
  onBulkAction: (
    value: BulkActionType,
    items: NoumMemberBasicFragment[],
  ) => void;
  onInviteMembers(): void;
};

export const NoumPageTable = ({
  networkStatus,
  fetchMore,
  refetch,
  data,
  currentCount,
  totalCount,
  rowsPerPage,
  onRowActionSelect,
  onBulkAction,
  onInviteMembers,
}: NoumPageTableTypes) => {
  const { modalType, openModal, closeModal } = useModalManager<ModalType>();
  const { statuses, roles } = useNoumMembersListFilters();

  const columns: TableColumn<NoumMemberBasicFragment>[] = [
    {
      id: 'name',
      renderValue: (item) => (
        <Stack align="center" gap={8}>
          <Avatar
            url={item.user?.profile?.profilePictureThumbnail}
            width={32}
          />
          <TSpan font="body-m-bold">{`${item?.user?.firstName} ${item?.user?.lastName}`}</TSpan>
        </Stack>
      ),
      title: 'Name',
      width: '30%',
    },
    {
      id: 'role',
      renderValue: (item) => <MemberRoleTag member={item} />,
      title: 'Role',
      width: 'auto',
    },
    {
      id: 'status',
      renderValue: (item) => <MemberStatusTag member={item} />,
      title: 'Status',
      width: 'auto',
    },
    {
      id: 'connected',
      renderValue: (item) => (
        <TSpan
          font="body-m"
          colorToken="--text-tablecell-header-neutral-default"
        >
          {item?.connectedAt
            ? getLocalTimeFormatted({
                dateTime: item?.connectedAt,
                returnFormat: 'dd/MM/yyyy, hh:mm a',
              })
            : null}
        </TSpan>
      ),
      title: 'Connected',
      wordWrap: false,
      width: '20%',
    },
    {
      id: 'actions',
      renderValue: (item) => {
        const menuOptions: ActionsMenuItem<RowActionType>[] = compact([
          MembersManagerActionPermissions.canViewManagerDetails(item) && {
            key: 'manager_details',
            value: 'manager_details',
            label: 'Manager Details',
            iconName: 'info_m',
          },
          MembersManagerActionPermissions.canEditRole(item) && {
            key: 'edit_role',
            value: 'edit_role',
            label: 'Edit Role',
            iconName: 'edit_m',
          },
          MembersManagerActionPermissions.canViewHomeNoum(item) && {
            key: 'view_home_noum',
            value: 'view_home_noum',
            label: 'View Home Noum',
            iconName: 'profile_m',
          },
          MembersManagerActionPermissions.canDisconnect(item) && {
            key: 'disconnect',
            value: 'disconnect',
            label: 'Disconnect',
            iconName: 'close_m',
            intent: 'danger',
          },
        ]);
        return menuOptions.length > 0 ? (
          <Stack justify="flex-end">
            <DataGrid.ActionsMenu<RowActionType>
              size="small"
              onClick={(value) => onRowActionSelect(value, item)}
              menuOptions={menuOptions}
            />
          </Stack>
        ) : null;
      },
      title: '',
    },
  ];

  const debouncedSubmit = useMemo(
    () =>
      debounce((values: NoumMembersListQueryFilters) => {
        refetch({
          input: {
            statuses: values.statuses,
            roleIDs: values.roleIDs,
            searchTerm: values.search,
            offset: 0,
            limit: rowsPerPage,
          },
        });
      }, 500),
    [refetch, rowsPerPage],
  );

  return (
    <SkeletonLoaderProvider isLoading={networkStatus === NetworkStatus.loading}>
      <DataGrid.Provider data={data}>
        <Stack gap={24} vertical align="stretch" maxHeight="100%">
          <DataGrid.Filters<NoumMembersListQueryFilters>
            submitOnChange={true}
            defaultValues={{
              search: '',
              roleIDs: undefined,
              statuses: undefined,
            }}
            onSubmit={debouncedSubmit}
          >
            <Stack gap={16} justify="space-between">
              <Stack gap={16} justify="flex-start">
                <DataGrid.FilterInput<NoumMembersListQueryFilters, 'statuses'>
                  name="statuses"
                  render={({ field: { value, onChange } }) => (
                    <MultiselectField
                      inputSize="small"
                      hideIcons
                      label="Status"
                      allOptionLabel="All"
                      options={statuses.options}
                      isLoading={statuses.loading}
                      value={value ?? statuses.allValues}
                      onChange={onChange}
                      maxContainerHeight="500px"
                      allSelectionStrategy="empty-means-all"
                    />
                  )}
                />
                <DataGrid.FilterInput<NoumMembersListQueryFilters, 'roleIDs'>
                  name="roleIDs"
                  render={({ field: { value, onChange } }) => (
                    <MultiselectField
                      inputSize="small"
                      hideIcons
                      label="Role"
                      allOptionLabel="All"
                      options={roles.options}
                      isLoading={roles.loading}
                      value={value ?? roles.allValues}
                      onChange={onChange}
                      maxContainerHeight="500px"
                      allSelectionStrategy="empty-means-all"
                    />
                  )}
                />
                <Button
                  size="small"
                  tertiary
                  leftIcon={<Icon name="info_m" size={24} />}
                  onClick={() => openModal('roles-info')}
                >
                  Roles
                </Button>
              </Stack>
              <Stack gap={16} justify="flex-start">
                <DataGrid.FilterInput<NoumMembersListQueryFilters, 'search'>
                  name="search"
                  render={({ field: { value, onChange } }) => (
                    <TextField
                      inputSize="small"
                      placeholder="Search..."
                      value={value}
                      onChange={onChange}
                      leftIcon={
                        <Icon
                          name="search_m"
                          size={24}
                          color="--icon-input-neutral-default"
                        />
                      }
                    />
                  )}
                />
                <Button
                  size="small"
                  secondary
                  leftIcon={<Icon name="add_m" size={22} />}
                  onClick={onInviteMembers}
                >
                  Invite Members
                </Button>
              </Stack>
            </Stack>
          </DataGrid.Filters>

          <Infinite
            onFetchMore={() =>
              fetchMore({ variables: { offset: currentCount } })
            }
            status={getBottomStatusFromQuery({
              networkStatus,
              currentCount,
              totalCount,
            })}
            disableFetchMoreWhileLoading
            isSpinnerRelative
          >
            <DataGrid.Table
              loading={[
                NetworkStatus.loading,
                NetworkStatus.setVariables,
              ].includes(networkStatus)}
              columns={columns}
              rowsPerPage={rowsPerPage}
              keyExtractor={(item) => item._id}
              enableRowSelection
              noResultsMessageRowSpan={1}
            />
          </Infinite>
        </Stack>

        <BulkActionsPopover onBulkAction={onBulkAction} />

        <RolesInfoModal
          isOpen={modalType === 'roles-info'}
          onClose={closeModal}
        />
      </DataGrid.Provider>
    </SkeletonLoaderProvider>
  );
};
