import { NoumMemberStatus } from '@/apollo/generated/types';
import { useGetNoumMembersQuery } from '@/apollo/graphql';
import { cleanList } from '@/utils/list';
import { useMemo } from 'react';

interface UseNoumMembersManagerOptions {
  noumId?: string;
  rowsPerPage: number;
}

export function useNoumMembersManagerList({
  noumId,
  rowsPerPage,
}: UseNoumMembersManagerOptions) {
  const { data, fetchMore, refetch, networkStatus } = useGetNoumMembersQuery({
    fetchPolicy: 'cache-and-network',
    notifyOnNetworkStatusChange: true,
    variables: {
      noumId: noumId!,
      input: {
        offset: 0,
        limit: rowsPerPage,
        statuses: [
          NoumMemberStatus.Connected,
          NoumMemberStatus.Invited,
          NoumMemberStatus.Requested,
        ],
      },
    },
    skip: !noumId,
  });

  const membersData = useMemo(
    () => cleanList(data?.getSpaceById?.members?.data),
    [data?.getSpaceById?.members?.data],
  );
  const currentCount = membersData.length;
  const totalCount = data?.getSpaceById?.members?.count ?? 0;

  return {
    membersData,
    currentCount,
    totalCount,
    networkStatus,
    fetchMore,
    refetch,
  };
}
